<template>
  <div>
    <b-card
      img-alt="Image"
      img-top
      tag="article"
      style="max-width: 20rem;"
      class="mb-2"
    >
      <b-card-text>
        {{id}}
      </b-card-text>

      <!-- <input @click="Play()" type="button" value="Play" /> -->
      <input @click="updateState()" v-model="active" type="checkbox" checked data-toggle="toggle" />
      <label for="checkbox">{{ active }}</label>
    </b-card>
  </div>
</template>

<script>
import {Howl} from 'howler';

export default {
  data() {
    return {
      active: false,
      path : "",
      audio_file: null
    };
  },
   created() {
    // this.path = this.file_path;
    this.audio_file = new Howl({
        src: [require(`@/assets/${this.file_path}.mp3`)],
        loop:true
        });
  },
  methods: {
    updateState(){
        if (this.active == true) {
            this.active = false;
            this.audio_file.stop();
            this.$store.state.audios = this.$store.state.audios.filter(obj => obj.id != this.id) 
            if (this.$store.state.audios.length == 0)  this.$root.$refs.recorder.StopRecord();
        }
        else {
            this.active = true;
            this.$store.state.audios.push({id:this.id, file:this.audio_file})
        }
        console.log(this.$store.state.audios)
    }
  },
    props:{
    file_path: {
      type: String,
      required: true
    },
    id :{
        type: Number,
        required: true
    }
  }
};
</script>

