<template>
  <div>
    <input :disabled="audio_list_size" @click="PlayAll()" type="button" value="Play" />
    <input :disabled="!audio_is_playing" @click="StopAll()" type="button" value="stop" />
  </div>
</template>

<script>
export default {
    data() {
        return {
        };
  },
    computed:{
        audio_list_size: function(){
            return this.$store.state.audios.length == 0
        },
        audio_is_playing: function(){
            return this.$store.state.audio_playing
        }

  },
  methods:{
      PlayAll(){
            Array.from(this.$store.state.audios).forEach(audio => {audio.file.play();})
            this.$store.state.audio_playing = true;
            console.log(this.$store.state.audios);
      },
      StopAll(){
            Array.from(this.$store.state.audios).forEach(audio => {audio.file.stop();})
            this.$store.state.audio_playing = false;
          console.log(this.$store.state.audios);
      },
  },
}
</script>

